<template>
    <div>
        <div class="main-alert-msg-item" :class="(actionStatus === 'delete') ? 'deleted-layout' : 'updated-layout'" v-if="actionOn === true">{{ actionMsg }}</div>
        <div class="main-datetime-area">
            <div class="main-numeric-date-area">
                <div>
                    {{ dayNumber }}
                    <span>
                        <i>{{ month }}</i>
                        {{ year }}
                    </span>
                </div>
            </div>
            <div class="main-text-date-area">
                {{ dayString }}

                <div class="main-weather-wedget-api-area">
                    <div class="main--data">
                        <span class="weather-icons" :class="`_${weatherApiData.weather[0].icon}`"></span>
                        {{ Math.ceil(weatherApiData.main.temp) }} <small>°C</small>
                    </div>
                </div>

                <span class="weather-country">{{ weatherApiData.name }}, {{ weatherApiData.sys.country }}</span>
            </div>
        </div>
    </div>
</template>

<script>
/* eslint-disable */
import moment from "moment";
import axios from 'axios';
import bus from "./../bus.js";

export default {
    data() {
        return {
            dayNumber: moment().format('D'),
            dayString: moment().format('dddd'),
            month: moment().format('MMM'),
            year: moment().format('YYYY'),

            actionOn: false,
            actionStatus: '',
            actionMsg: '',

            weatherApiData: {
                weather: [{
                    icon: ""
                }],
                main: {
                    temp_max: "__"
                },
                name: "",
                sys: {
                    country: ""
                }
            }
        }
    },
    created() {
        this.listenToEvents();
        this.getWeatherApiInfo();
    },
    methods: {
        // deleteActionOn(el){
        //     if(el === true) actionOn: true;
        // },

        async getWeatherApiInfo() {
            try {
                const data = await axios({
                    method: "GET",
                    url: "https://api.openweathermap.org/data/2.5/weather?q=Rabat&appid=886705b4c1182eb1c69f28eb8c520e20&units=metric",
                });
                this.weatherApiData = data.data;

                bus.$emit("weather-data-loader");
            } catch (err) {
                console.log(err.message);
            }
        },

        listenToEvents() {
            bus.$on("deleteItem", $event => {
                this.actionOn = true;
                this.actionStatus = 'delete';
                this.actionMsg = 'Item deleted!';
                setTimeout(() => { this.actionOn = false; }, 1500);
            });

            bus.$on("editItem", $event => {
                this.actionOn = true;
                this.actionStatus = '';
                this.actionMsg = 'Item edited!';
                setTimeout(() => { this.actionOn = false; }, 1500);
            });
        },
    },
}
</script>

