<template>
  <!--  -->
  <div 
    class="col align-self-center"
  >

    <div class="main-todo-ui-article" v-for="(todo, index) in todos" :key="todo.id">
      <div class="main-todos-list-area">
        <div class="main-todo-item-area">
          <div class="main-todo-item-fields-area action-elem">
            <label class="main-checkbox-btn-setting-elem">
              <input
                type="checkbox"
                v-model="todo.done"
                :checked="todo.done"
                :value="todo.done"
                @change="updateTodo(todo)"
                title="Mark as done?"
              />
              <i class="btn-setting update-btn" v-bind:class = "(todo.done)?'checked':''"></i>
            </label>
          </div>
          <!-- @keyup="updateTodo(todo)" -->
          <textarea class="main-todo-item-fields-area field-elem textarea" 
            type="text"
            :class="todo.done?'todo__done':'' && textareaFocus?'isFocus':''"
            v-model="todo.title"

            @input="resizeTextarea"
            ref="textarea"

            @keypress="todo.editing=true"
            @change="updateTodo(todo)"
            @focus="addOnFocusClass($event, index)"
            @blur="removeOnFocusClass"

            :disabled="todo.done"
          ></textarea>
          <div class="main-todo-item-fields-area action-elem">
            <div class="text-align-right">
              <span
                title="Delete todo?"
                @click="deleteTodo(todo._id)"
                v-if="!actionBtn[index]"
              >
                <i class="btn-setting delete-btn"></i>
              </span>

              <span
                v-if="actionBtn[index]"
                class="main-auto-save-msg--elem"
              >
                <div>Auto</div><div>Save</div>
              </span>

              <!-- <span
                title="Edit todo?"
                @click="updateTodo(todo)"
                v-if="actionBtn[index]"
              >
                <i class="btn-setting edit-btn"></i>
              </span> -->
            </div>
          </div>
        </div>
      </div>
    </div>

    <div
      class="main-no-todo-ui-article"
      v-if="todos.length === 0" 
    >
      No more todos right now <span>😉</span>
    </div>

  </div>
</template>

<script>
/* eslint-disable */
import axios from "axios";
import bus from "./../bus.js";

export default {
  data() {
    return {
      todos: [],
      actionBtn: [],
      blured: false,
      doneLoading: false,
      textareaFocus: false
    };
  },
  created: function() {
    this.fetchTodo();
    this.listenToEvents();
  },
  updated() {
    this.getResizeTextarea();
  },
  mounted() {
		this.$nextTick(function() {
      window.addEventListener('resize', this.getResizeTextarea);
      this.getResizeTextarea
		})
  },
  beforeDestroy() {
    window.removeEventListener('resize', this.getResizeTextarea);
  },
  watch: {
    $route: function() {
      let self = this;
      self.doneLoading = false;
      self.fetchData().then(function() {
        self.doneLoading = true;
      });
    }
  },
  methods: {
    async fetchTodo(){
      this.$http.get("/").then(response => {
        const data = response.data.data.data
        const sortByCreatedAt = data.sort((a, b) => (new Date(b.createdAt) - new Date(a.createdAt)) ); //updatedAt || createdAt
        const sortByDone = sortByCreatedAt.sort((a, b) => a.done - b.done);
        this.todos = sortByDone;

        bus.$emit("todos-data-loader");
      });
    },

    updateTodo(todo) {
      let id = todo._id;

      if(todo.title === "") console.log('delete item');

      this.$http
        .patch(`/${id}`, todo)
        .then(response => {
          this.fetchTodo();
          setTimeout(() => { this.actionBtn = []; }, 125);
          bus.$emit("editItem");
        })
        .catch(error => {
          console.log(error);
        });
    },

    deleteTodo(id) {
      this.$http.delete(`/${id}`).then(response => {
        this.fetchTodo();
        bus.$emit("deleteItem");
      });
    },

    resizeTextarea(event) {
        // console.log(event.code);
        event.target.style.minHeight = '27.5px';

        let scrllH = event.target.scrollHeight;
        if(event.code == "Enter") scrllH = event.target.scrollHeight + 22;
        event.target.style.minHeight = (scrllH) + 'px';
    },

    addOnFocusClass(event, i){
      this.removeOnFocusClass();
      this.blured = true;
      this.actionBtn[i] = true;

      event.target.offsetParent.parentNode.classList.add('isFocus');
    },

    removeOnFocusClass(){
      this.actionBtn = [];
      const elems = document.querySelectorAll(".main-todo-ui-article");
      [].forEach.call(elems, function(el) {
          el.classList.remove("isFocus");
      });
    },

    removeEditBtn(){
      console.log('remove edit btn!');
    },

    listenToEvents() {
      bus.$on("refreshTodo", $event => {
        this.actionBtn = [];
        this.fetchTodo(); //update todo
      });
    },

    // resize setting textarea function
    getResizeTextarea(event) {
      if(this.todos != 0){
        for(let i=0; i < this.$refs.textarea.length; i++){
          this.$refs.textarea[i].style.minHeight = '27.5px';
          this.$refs.textarea[i].style.minHeight = this.$refs.textarea[i].scrollHeight + 'px'; 
        }
      }
    }
  }
};
</script>

