<template>
  <div class="col align-self-center sticky-position">
    <form class="sign-in" @submit.prevent>
      <div class="main-submit-area">
        <input
          type="text"
          class="main-create-title-input-area"
          @keypress="typing=true"
          placeholder="What do you want to do?"
          v-model="title"
          @keyup.enter="addTodo($event)"
        />
        <small class="main-msg-submit-text-area" v-show="title">Save with <img src="/img/svg/enter-key.svg" alt="#"> Enter Keyboard.</small>
      </div>
    </form>
  </div>
</template>
<script>
/* eslint-disable */
import axios from 'axios';
import bus from "./../bus.js";

export default {
  data() {
    return {
      title: "",
      typing: false
    };
  },
  methods: {
    addTodo(event) {
      if (event) event.preventDefault();
      let todo = {
        title: this.title,
        done: false //false by default
      };

      this.$http
        .post("/", todo)
        .then(response => {
          this.clearTodo();
          this.refreshTodo();
          this.typing = false;
        })
        .catch(error => {
          console.log(error);
        });
    },

    clearTodo() {
      this.title = "";
    },

    refreshTodo() {
      bus.$emit("refreshTodo");
    }
  }
};
</script>
