<template>
    <transition name="fade">
        <div 
            class="main-loading-page-area"
            v-if="(todosDataLaoded === true || weatherDataLaoded === true)"
        >
            <div class="main-loading-page-content-area" v-if="removeLoadingContent === true">
                <img src="/img/loading.gif" alt="#">
                <small>Loading Page</small>
            </div>
        </div>
    </transition>
</template>

<script>
/* eslint-disable */
import moment from "moment";
import axios from 'axios';
import bus from "./../bus.js";

export default {
    data() {
        return {
            todosDataLaoded: true,
            weatherDataLaoded: true,
            removeLoadingContent: true,
        }
    },
    created() {
        this.listenToEvents();
    },
    methods: {
        listenToEvents() {
            bus.$on("todos-data-loader", $event => {
                setTimeout(() => { this.todosDataLaoded = false; }, 2250);
            });

            bus.$on("weather-data-loader", $event => {
                setTimeout(() => { this.removeLoadingContent = false; }, 2000);
                setTimeout(() => { this.weatherDataLaoded = false; }, 2250);
            });

            // If there is something wrong
            setTimeout(() => { 
                this.weatherDataLaoded = false; 
                this.weatherDataLaoded = false; 
            }, 2500);
        }
    },
}
</script>

