.main-loading-page-area{
    position: fixed;
    top: 0;
    left: 0;
    width: 100vw;
    height: 100vh;
    background-image: linear-gradient(to bottom right, #f7f7fa, #f7f7fa, #f7f7fa, #ECEFF1);
    z-index: 99997;
